import React, { useEffect, useState } from 'react';
import Background_music_1 from '../../assets/common/audio/Background_music_1.mp3';
import For_level_completion from '../../assets/common/audio/For_level_completion.mp3';
import GL_A_19 from '../../assets/common/audio/GL_A_19.mp3';
import GL_A_5 from '../../assets/common/audio/GL_A_5.mp3';
import Audio_Replay from '../../assets/common/textures/interactions/Audio_Replay.png';
import Hand_Cursor3 from '../../assets/common/textures/interactions/Hand_Cursor3.png';
import Speech_Bubble from '../../assets/common/textures/interactions/Speech_Bubble.png';
import Speech_Bubble_2 from '../../assets/common/textures/interactions/Speech_Bubble_2.png';
import inactive_audio_replay from '../../assets/common/textures/interactions/inactive_audio_replay.png';
import lets_play from '../../assets/common/textures/interactions/lets_play.png';
import Mascot1 from '../../assets/common/textures/mascot/Mascot1.png';
import Mascot3 from '../../assets/common/textures/mascot/Mascot3.png';
import VBMV_A_L1_1 from '../../assets/level1/audio/VBMV_A_L1_1.mp3';
import VBMV_A_L1_2 from '../../assets/level1/audio/VBMV_A_L1_2.mp3';
import VBMV_A_L1_5 from '../../assets/level1/audio/VBMV_A_L1_5.mp3';
import VBMV_A_L2_1 from "../../assets/level2/audio/VBMV_A_L2_1.mp3";
import VBMV_A_L2_2 from "../../assets/level2/audio/VBMV_A_L2_2.mp3";
import VBMV_A_L2_3 from "../../assets/level2/audio/VBMV_A_L2_3.mp3";
import VBMV_A_L2_4 from "../../assets/level2/audio/VBMV_A_L2_4.mp3";
import VBMV_A_L2_5 from "../../assets/level2/audio/VBMV_A_L2_5.mp3";
import Header from '../components/common/Header';
import PlayAgain from '../components/common/Play_Agian/PlayAgain';
import Threedots from '../components/common/Threedots/Threedots';
import { level2Data, level2DemoData } from '../constants/level2';
import './index.scss';
import { shuffleArray } from '../helper';
import Mascot from '../components/common/Mascot';


const WHITE_LIST_DEMO_INDEX = [];
level2DemoData.forEach((item, index) => {
  if (item.isCorrect) {
    WHITE_LIST_DEMO_INDEX.push(index)
  }
})  //index of correct answers in demo json

let wrongOptionCount = 0;

const Index = ({ setCurrentLevelState }) => {
  const [startPlay, setStartPlay] = useState(true);
  const [playGame, setPlayGame] = useState(true);
  const [showMascot, setShowMascot] = useState(false);
  const [showSecondMascot, setShowSecondMascot] = useState(false);
  const [playThirdAudio, setPlayThirdAudio] = useState(false);
  const [isMusicPlaying, setMusicPlaying] = useState(true);
  const [shouldPlayMusic, setShouldPlayMusic] = useState(false);
  const [showGrid, setShowGrid] = useState(false)
  const [soundFilesEnded, setSoundFilesEnded] = useState(false);
  const [levelData, setLevelData] = useState([...level2DemoData])
  const [showGreenTick, setShowGreenTick] = useState(false);
  const [showCursorHand, setShowCursorHand] = useState(true);
  const [playFourthAudio, setPlayFourthAudio] = useState(false);
  const [playLevelThreeAudio, setPlayLevelThreeAudio] = useState(false);
  const [showGridSet, setShowGridSet] = useState(false)
  const [levelData2, setLevelData2] = useState(level2Data[0])

  const [selectedImageId, setSelectedImageId] = useState(null);
  const [showTick, setShowTick] = useState(false);
  const [isCorrectImageClicked, setIsCorrectImageClicked] = useState(false);
  const [gameIndex, setGameIndex] = useState(0)
  const [showAudioReplayCursor, setShowAudioReplayCursor] = useState(false);
  const [audioReplayEnded, setAudioReplayEnded] = useState(false);
  // Audio replay button state
  const [isAudioPlaying, setIsAudioPlaying] = useState(false);
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);
  const [isAudioReplayButtonActive, setIsAudioReplayButtonActive] =
    useState(true);

  const [score, setScore] = useState(10);
  const [whitelistDemoIndex, setWhitelistDemoIndex] = useState([])


  const [rightAnswerArray, setRightAnswerArray] = useState([]);

  const [progress, setProgress] = useState(0);

  const updateScore = (newScore) => {
    setScore(newScore);
  };

  /**
   * Mute Unmute Function
   */
  const handleMusicButtonClick = () => {
    setShouldPlayMusic(!shouldPlayMusic);
    setMusicPlaying(!isMusicPlaying);
  };

  useEffect(() => {
    const audio = new Audio(Background_music_1);

    if (shouldPlayMusic && isMusicPlaying) {
      audio.loop = true;
      audio.play();
    } else {
      audio.pause();
    }

    return () => {
      audio.pause();
      audio.currentTime = 0;
    };
  }, [shouldPlayMusic, isMusicPlaying]);

  const sequentialSelectDemoAnswer = (index) => {
    let array = [...whitelistDemoIndex];
    array.push(WHITE_LIST_DEMO_INDEX[index])
    console.log(array)
    new Audio(levelData[0].correct_sound).play()
    setWhitelistDemoIndex(array)
    // setShowCursorHand(true);

  }

  useEffect(() => {
    setLevelData(level2DemoData)
    if (whitelistDemoIndex.length < WHITE_LIST_DEMO_INDEX.length && whitelistDemoIndex.length > 0) {
      setTimeout(() => {
        sequentialSelectDemoAnswer(whitelistDemoIndex.length)
      }, 2000)
    }
    if (whitelistDemoIndex.length === WHITE_LIST_DEMO_INDEX.length) {
      setTimeout(() => {
        setShowCursorHand(false)
        setPlayLevelThreeAudio(true);
      }, 2000)
    }
    // console.log(whitelistDemoIndex)
  }, [whitelistDemoIndex])

  useEffect(() => {
    if (rightAnswerArray.length === WHITE_LIST_DEMO_INDEX.length) {
      setShowCursorHand(true)
      let i = gameIndex;
      i++
      setTimeout(() => {
        const correctAnswer = new Audio(levelData2[0].correct_answer);
        correctAnswer.play();
        setTimeout(() => {
          setGameIndex(i)
          setIsCorrectImageClicked(true);
          if ((gameIndex + 1) % 2 === 0) {
            let updatedProgress = progress;
            updatedProgress = updatedProgress + 20;
            console.log(gameIndex, updatedProgress)
            setProgress(updatedProgress)
          }
          // setRightAnswerArray([])
        }, 1500)
      }, 1500);
    }
  }, [rightAnswerArray])

  useEffect(() => {
    setTimeout(() => {
      if (gameIndex < level2Data.length) {
        setRightAnswerArray([])
        let gameArray = [...level2Data[gameIndex]]
        gameArray = shuffleArray(gameArray)
        setLevelData2(gameArray)
        gameIndex > 0 && setShowCursorHand(false)
      }

      if (gameIndex === level2Data.length) {
        setShowGridSet(false);
        const audio = new Audio(For_level_completion);
        audio.addEventListener('play', () => {
          setScore(20);
          var count = 200;
          var defaults = {
            origin: { y: 0.7 }
          };

          function fire(particleRatio, opts) {
            confetti(Object.assign({}, defaults, opts, {
              particleCount: Math.floor(count * particleRatio)
            }));
          }

          fire(0.25, {
            spread: 26,
            startVelocity: 55,
          });
          fire(0.2, {
            spread: 60,
          });
          fire(0.35, {
            spread: 100,
            decay: 0.91,
            scalar: 0.8
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 25,
            decay: 0.92,
            scalar: 1.2
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 45,
          });
        });
        audio.play();

        setSoundFilesEnded(true);
        const playNewGameAudio = new Audio(GL_A_5);
        playNewGameAudio.play();
      }
      setShowTick(false)
      setIsCorrectImageClicked(false);
      setTimeout(() => {
        gameIndex > 0 && gameIndex < level2Data.length && new Audio(level2Data[gameIndex][0].find_vegetable).play()
      }, 500)
    }, 1500);
  }, [gameIndex, setGameIndex]);

  /**
   * onclick play button
   */
  const PlayButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
    setScore(10);
  };
  /**
   * onclick play button
   */
  const PlayAgainButton = () => {
    // setShouldPlayMusic(true);
    // setStartPlay(false);
    // setShowMascot(true);
    // setSoundFilesEnded(false);
    // setScore(10);
    window.location.reload();
  };

  /**
   * First Audio End 
   */
  const handleAudioEnd = () => {
    setShowMascot(false);
    setShowSecondMascot(true);
    setShowGrid(true);
  };

  /**
   * Second Audio End 
   */
  const handleAudioEndSecond = () => {
    setPlayThirdAudio(true);
    setShowSecondMascot(false);
    setShowGrid(true);

  };

  // play third audio and fourth audio
  useEffect(() => {
    if (playLevelThreeAudio && whitelistDemoIndex.length === WHITE_LIST_DEMO_INDEX.length) {
      const audio = new Audio(level2DemoData[0].correct_answer);
      audio.addEventListener('ended', () => {
        setPlayFourthAudio(true);
      });
      audio.play();
    }
  }, [playLevelThreeAudio]);

  const handleThirdAudioEnd = () => {
    whitelistDemoIndex.length === 0 && sequentialSelectDemoAnswer(whitelistDemoIndex.length)
    // setTimeout(() => {
    // 
    // }, 1500);
  };

  // when fourth audio end call this function
  const handleFourthAudioEnd = () => {
    setShowGrid(false);
    setShowGridSet(true);
    const audioReplay = new Audio(GL_A_19);
    setShowAudioReplayCursor(true);
    const nextAudio = new Audio(levelData2[0].find_vegetable);
    audioReplay.addEventListener('ended', () => {
      nextAudio.play();
      setShowAudioReplayCursor(false);
      setAudioReplayEnded(true);
    });

    audioReplay.play();
  };

  // handleImageClick function
  const handleImageClick = (item, index) => {
    if (isCorrectImageClicked) {
      // Disable image click if correct image is already clicked
      return;
    }

    setSelectedImageId(item.id);

    if (item.isCorrect) {
      const correctSound = new Audio(item.correct_sound);
      correctSound.play();

      let array = [...rightAnswerArray]
      array.push(index)
      setRightAnswerArray(array)

    } else {
      const wrongSound = new Audio(item.wrong_sound);
      wrongSound.addEventListener("ended", () => {
        const audio = new Audio(item.find_vegetable);
        audio.addEventListener("play", () => {
          setIsCorrectImageClicked(true);
        });
        audio.addEventListener("ended", () => {
          setIsCorrectImageClicked(false);
        });
        audio.play();
      });
      wrongSound.play();
  
      const wrongImage = document.getElementById(`image-${item.id}`);
      wrongImage.classList.add("rise-shake");
      setTimeout(() => {
        wrongImage.classList.remove("rise-shake");
      }, 830);
    }
  };

  // gameInstructionAudio
  const gameInstructionAudio = (levelData) => {
    if (!isButtonDisabled) {
      const audio = new Audio(levelData[gameIndex].find_vegetable);
      setIsAudioPlaying(true);
      setIsButtonDisabled(true);
      audio.addEventListener('ended', () => {
        setIsAudioPlaying(false);
        setIsButtonDisabled(false);
      });
      audio.play();
    }
  };

  console.log(showCursorHand)

  return (
    <div className='level2Styles'>
      <div className="level0_div">
        <div>
          <Header onMusicButtonClick={handleMusicButtonClick} isMusicPlaying={isMusicPlaying} score={score} selectedLevel={2} percentFill={progress} />
          <div className='audio_replay d-flex justify-content-end'>
          <div className="audio_replay_button">
              {audioReplayEnded && (
                <button
                  className="btn p-0"
                  type="button"
                  onClick={() => gameInstructionAudio(levelData2)}
                  disabled={isButtonDisabled}
                >
                  {isAudioPlaying || isAudioReplayButtonActive ? (
                    <img
                      src={Audio_Replay}
                      className="img-fluid Audio_Replay_Btn"
                      alt="Audio_Replay"
                    />
                  ) : (
                    <img
                      src={inactive_audio_replay}
                      className="img-fluid Audio_Replay_Btn"
                      alt="Audio_Replay"
                    />
                  )}
                </button>
              )}
              {!audioReplayEnded && (
                <img
                  src={inactive_audio_replay}
                  className="img-fluid Audio_Replay_Btn"
                  alt="Audio_Replay"
                />
              )}
              {showAudioReplayCursor && (
                <img
                  src={Hand_Cursor3}
                  className="audio_replay"
                  alt="hand_cursor"
                />
              )}
            </div>
          </div>
        </div>
        {startPlay && (
          <div className="play_btn_div">
            <button
              className="btn play_btn p-0"
              type="button"
              onClick={() => {
                PlayButton();
              }}
            >
              <img src={lets_play} className="play_img img-fluid" alt="play_btn" />
            </button>
            <span></span>
          </div>
        )}
        {showMascot && (
          <Mascot />
        )}

        {showSecondMascot && (<Mascot showMascot2={true} />)}
        {showGrid && (
          <div className='vegetableGridContainer'>
            <div className='vegetablesGrid'>
              {levelData.map((item, index) => (
                <div className='containerStyle' key={item.id} style={!whitelistDemoIndex.includes(index) && !showCursorHand ? { visibility: 'hidden', position: 'relative' } : { position: 'relative' }}>
                  <img src={item.img} className='imageStyle' alt='Big Image' />
                  {whitelistDemoIndex.includes(index) && (<img src={item.green_tick} style={{ position: 'absolute', width: '60px' }} alt='Green Tick' />)}
                  {showCursorHand && index === whitelistDemoIndex[whitelistDemoIndex.length - 1] && (<img src={item.hand_cursor} className='hand_cursor' alt='hand_cursor' />)}
                </div>
              ))
              }
            </div>
          </div>
        )}
        {playFourthAudio && (<audio src={VBMV_A_L2_5} autoPlay onEnded={handleFourthAudioEnd} />)}

        {showGridSet && (
          <div className='vegetableGridContainer'>
            <div className='vegetablesGrid'>
              {levelData2.length > 0 && levelData2.map((item, index) => (
                <div
                  className={`containerStyle ${item.id === selectedImageId ? 'selected' : ''}`}
                  key={item.id}
                  onClick={() => handleImageClick(item, index)}
                  style={!rightAnswerArray.includes(index) && showCursorHand ? { visibility: 'hidden', position: 'relative' } : { position: 'relative' }}
                >
                  <img
                    id={`image-${item.id}`}
                    src={item.img}
                    className='imageStyle'
                    alt='Big Image'
                  />
                  {item.isCorrect && rightAnswerArray.includes(index) && (
                    <img src={item.green_tick} className='greenTick' style={{ position: 'absolute' }} alt='Green Tick' />
                  )}
                </div>
              ))}
            </div>
          </div>
        )}

        {soundFilesEnded && (
          <PlayAgain PlayAgainButton={PlayAgainButton} setCurrentLevelState={() => {
            localStorage.setItem('currentLevel', 3);
            window.location.reload()
            setCurrentLevelState(3)
          }} />
        )}

      </div>
      {showMascot && <audio src={VBMV_A_L2_1} onEnded={handleAudioEnd} autoPlay />}
      {showSecondMascot && <audio src={VBMV_A_L2_2} onEnded={handleAudioEndSecond} autoPlay />}
      {playThirdAudio && <audio src={VBMV_A_L2_3} onEnded={handleThirdAudioEnd} autoPlay />}

    </div>
  );
};

export default Index;
