import React, { useState, useRef, useEffect } from 'react';
import './index.scss';
import Header from '../components/common/Header';
import lets_play from '../../assets/common/textures/interactions/lets_play.png';
import Playagain from '../../assets/common/textures/interactions/Playagain.png';
import Playgame from '../../assets/common/textures/interactions/Playgame.png';
import Audio_Replay from '../../assets/common/textures/interactions/Audio_Replay.png';
import inactive_audio_replay from '../../assets/common/textures/interactions/inactive_audio_replay.png';
import Hand_Cursor3 from '../../assets/common/textures/interactions/Hand_Cursor3.png';
import Mascot3 from '../../assets/common/textures/mascot/Mascot3.png';
import Mascot1 from '../../assets/common/textures/mascot/Mascot1.png';
import Speech_Bubble from '../../assets/common/textures/interactions/Speech_Bubble.png';
import Speech_Bubble_2 from '../../assets/common/textures/interactions/Speech_Bubble_2.png';
import VBMV_A_L1_1 from '../../assets/level1/audio/VBMV_A_L1_1.mp3';
import VBMV_A_L1_2 from '../../assets/level1/audio/VBMV_A_L1_2.mp3';
import VBMV_A_L1_3 from '../../assets/level1/audio/VBMV_A_L1_3.mp3';
import VBMV_A_L1_4 from '../../assets/level1/audio/VBMV_A_L1_4.mp3';
import VBMV_A_L1_5 from '../../assets/level1/audio/VBMV_A_L1_5.mp3';
import VBMV_A_L0_7 from '../../assets/level0/audio/VBMV_A_L0_7.mp3';
import Background_music_1 from '../../assets/common/audio/Background_music_1.mp3';
import For_level_completion from '../../assets/common/audio/For_level_completion.mp3';
import GL_A_19 from '../../assets/common/audio/GL_A_19.mp3';
import GL_A_5 from '../../assets/common/audio/GL_A_5.mp3';
import Threedots from '../components/common/Threedots/Threedots';
import { level1DemoData } from '../constants';
import { level1Data } from '../constants';
import Canvas from '../components/common/Canvas_Confetti/canvas';
import PlayAgain from '../components/common/Play_Agian/PlayAgain';
import Mascot from '../components/common/Mascot';

const Index = ({ setCurrentLevelState }) => {
  const [startPlay, setStartPlay] = useState(true);
  const [playGame, setPlayGame] = useState(true);
  const [showMascot, setShowMascot] = useState(false);
  const [showSecondMascot, setShowSecondMascot] = useState(false);
  const [playThirdAudio, setPlayThirdAudio] = useState(false);
  const [isMusicPlaying, setMusicPlaying] = useState(true);
  const [shouldPlayMusic, setShouldPlayMusic] = useState(false);
  const [showGrid, setShowGrid] = useState(false)
  const [soundFilesEnded, setSoundFilesEnded] = useState(false);
  const [levelData, setLevelData] = useState([...level1DemoData])
  const [showGreenTick, setShowGreenTick] = useState(false);
  const [showCursorHand, setShowCursorHand] = useState(false);
  const [playFourthAudio, setPlayFourthAudio] = useState(false);
  const [playLevelThreeAudio, setPlayLevelThreeAudio] = useState(false);
  const [showGridSet, setShowGridSet] = useState(false)
  const [levelData1, setLevelData1] = useState(level1Data[0])
  const [showModal, setShowModal] = useState({
    display: false,
    index: 0
  })
  const [selectedImageId, setSelectedImageId] = useState(null);
  const [showTick, setShowTick] = useState(false);
  const [isCorrectImageClicked, setIsCorrectImageClicked] = useState(false);
  const [gameIndex, setGameIndex] = useState(0)
  const [showAudioReplayCursor, setShowAudioReplayCursor] = useState(false);
  const [audioReplayEnded, setAudioReplayEnded] = useState(false);
  // Audio replay button state
  const [isAudioPlaying, setIsAudioPlaying] = useState(false);
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);

  const [score, setScore] = useState(0);
  const [progress, setProgress] = useState(0);

  const updateScore = (newScore) => {
    setScore(newScore);
  };

  /**
   * Mute Unmute Function
   */
  const handleMusicButtonClick = () => {
    setShouldPlayMusic(!shouldPlayMusic);
    setMusicPlaying(!isMusicPlaying);
  };

  useEffect(() => {
    const audio = new Audio(Background_music_1);

    if (shouldPlayMusic && isMusicPlaying) {
      audio.loop = true;
      audio.play();
    } else {
      audio.pause();
    }

    return () => {
      audio.pause();
      audio.currentTime = 0;
    };
  }, [shouldPlayMusic, isMusicPlaying]);

  useEffect(() => {
    setTimeout(() => {
      if (gameIndex < level1Data.length)
        setLevelData1(level1Data[gameIndex])
      if (gameIndex === level1Data.length) {
        setShowGridSet(false);
        const audio = new Audio(For_level_completion);
        audio.addEventListener('play', () => {
          setScore(10);
          var count = 200;
          var defaults = {
            origin: { y: 0.7 }
          };

          function fire(particleRatio, opts) {
            confetti(Object.assign({}, defaults, opts, {
              particleCount: Math.floor(count * particleRatio)
            }));
          }

          fire(0.25, {
            spread: 26,
            startVelocity: 55,
          });
          fire(0.2, {
            spread: 60,
          });
          fire(0.35, {
            spread: 100,
            decay: 0.91,
            scalar: 0.8
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 25,
            decay: 0.92,
            scalar: 1.2
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 45,
          });
        });
        audio.play();

        setSoundFilesEnded(true);
        const playNewGameAudio = new Audio(GL_A_5);
        playNewGameAudio.play();
      }
      setShowTick(false)
      setIsCorrectImageClicked(false);

      gameIndex > 0 && gameIndex < level1Data.length && new Audio(level1Data[gameIndex][0].find_vegetable).play()

    }, 1500);
  }, [gameIndex, setGameIndex]);


  // useEffect(() => {

  // }, [showCursorHand, setShowCursorHand])


  /**
   * onclick play button
   */
  const PlayButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
  };
  /**
   * onclick play button
   */
  const PlayAgainButton = () => {

    window.location.reload();
  };

  /**
   * First Audio End 
   */
  const handleAudioEnd = () => {
    setShowMascot(false);
    setShowSecondMascot(true);
  };

  /**
   * Second Audio End 
   */
  const handleAudioEndSecond = () => {
    setPlayThirdAudio(true);
    setShowSecondMascot(false);
    setShowGrid(true);
  };

  // play third audio and fourth audio
  useEffect(() => {
    if (playLevelThreeAudio) {
      const audio = new Audio(VBMV_A_L0_7);
      audio.addEventListener('ended', () => {
        setPlayFourthAudio(true);
      });
      audio.play();
    }
  }, [playLevelThreeAudio]);

  const handleThirdAudioEnd = () => {

    setPlayLevelThreeAudio(true);
    setShowCursorHand(true);

  };

  // when fourth audio end call this function
  const handleFourthAudioEnd = () => {
    setShowGrid(false);
    setShowGridSet(true);
    const audioReplay = new Audio(GL_A_19);
    setShowAudioReplayCursor(true);
    const nextAudio = new Audio(VBMV_A_L1_5);
    audioReplay.addEventListener('ended', () => {
      nextAudio.play();
      setShowAudioReplayCursor(false);
      setAudioReplayEnded(true);
    });

    audioReplay.play();
  };

  // handleImageClick function
  const handleImageClick = (item, index) => {
    if (isCorrectImageClicked) {
      // Disable image click if correct image is already clicked
      return;
    }

    // Disable image click
    setIsCorrectImageClicked(true);

    setSelectedImageId(item.id);

    if (item.isCorrect) {
      const correctSound = new Audio(item.correct_sound);
      correctSound.addEventListener('ended', () => {
        const correctAnswer = new Audio(item.correct_answer);
        correctAnswer.addEventListener('ended', () => {
          // Enable image click after correct_answer audio ends
          setIsCorrectImageClicked(false);
        });

        correctAnswer.play();
      });

      correctSound.play();

      // setShowCursorHand(true);
      setShowTick(true);

      let i = gameIndex;
      i++;

      setTimeout(() => {
        setGameIndex(i);
      }, 1500);

      // increase progress bar
      if ((gameIndex + 1) % 2 === 0) {
        let updatedProgress = progress;
        updatedProgress = updatedProgress + 20;
        console.log(gameIndex, updatedProgress);
        setProgress(updatedProgress);
      }
    } else {
      const wrongSound = new Audio(item.wrong_sound);
      wrongSound.addEventListener('ended', () => {
        const findVegetableAudio = new Audio(item.find_vegetable);
        findVegetableAudio.addEventListener('ended', () => {
          // Enable image click after find_vegetable audio ends
          setIsCorrectImageClicked(false);
        });

        findVegetableAudio.play();
      });

      wrongSound.play();

      const wrongImage = document.getElementById(`image-${item.id}`);
      wrongImage.classList.add('rise-shake');
      setTimeout(() => {
        wrongImage.classList.remove('rise-shake');
      }, 830);
    }
  };


  const gameInstructionAudio = () => {
    if (!isButtonDisabled) {
      const currentGameIndex = gameIndex % levelData1.length;
      const currentItem = levelData1[currentGameIndex];

      if (currentItem) {
        const findVegetableAudio = new Audio(currentItem.find_vegetable);

        findVegetableAudio.addEventListener("play", () => {
          setIsCorrectImageClicked(true);
          setIsAudioPlaying(true);
          setIsButtonDisabled(true);
        });

        findVegetableAudio.addEventListener("ended", () => {
          setIsCorrectImageClicked(false);
          setIsAudioPlaying(false);
          setIsButtonDisabled(false);
        });

        findVegetableAudio.play();
      }
    }
  };




  return (
    <div className='level1Style'>
      <div className="level0_div">
        <div>
          <Header onMusicButtonClick={handleMusicButtonClick} isMusicPlaying={isMusicPlaying} score={score} selectedLevel={1} percentFill={progress} />
          <div className='audio_replay d-flex justify-content-end'>
            <div className='audio_replay_button'>
              {audioReplayEnded ? (
                <button
                  className="btn p-0"
                  type="button"
                  onClick={() => gameInstructionAudio()}
                  disabled={isButtonDisabled}
                >
                  {isAudioPlaying ? (
                    <img src={inactive_audio_replay} className="img-fluid  Audio_Replay_Btn" alt="Audio_Replay" />
                  ) : (
                    <img src={Audio_Replay} className="img-fluid  Audio_Replay_Btn" alt="Audio_Replay" />
                  )}
                </button>
              ) : (
                <img src={inactive_audio_replay} className="img-fluid Audio_Replay_Btn" alt="Audio_Replay" />
              )}
              {showAudioReplayCursor && <img src={Hand_Cursor3} className='audio_replay' alt='hand_cursor' />}
            </div>
          </div>
        </div>
        {startPlay && (
          <div className="play_btn_div">
            <button
              className="btn play_btn p-0"
              type="button"
              onClick={() => {
                PlayButton();
              }}
            >
              <img src={lets_play} className="play_img img-fluid" alt="play_btn" />
            </button>
            <span></span>
          </div>
        )}
        {showMascot && (
          <Mascot />
        )}

        {showSecondMascot && (<Mascot showMascot2={true} />)}


        {showGrid && (
          <div className='vegetableGridContainer'>
            <div className='vegetablesGrid'>
              {levelData.map((item, index) => (
                <div className='containerStyle' key={item.id} style={{ position: 'relative' }}>
                  <img src={item.big_img} className='imageStyle' alt='Big Image' />
                  {showCursorHand && index === 3 && (<img src={item.green_tick} style={{ position: 'absolute' }} alt='Green Tick' />)}
                  {!showCursorHand && index === 3 && <img src={item.hand_cursor} style={{ position: 'absolute' }} className='hand_cursor' alt='hand_cursor' />}
                </div>
              ))
              }
            </div>
          </div>
        )}
        {showGrid && showCursorHand && (
          <>
            <audio src={levelData[3].correct_sound} autoPlay onEnded={handleThirdAudioEnd} />
            {playFourthAudio && <audio src={VBMV_A_L1_4} autoPlay onEnded={handleFourthAudioEnd} />}
          </>
        )}

        {showGridSet && (
          <div className='vegetableGridContainer'>
            <div className='vegetablesGrid'>
              {levelData1.length > 0 && levelData1.map((item, index) => (
                <div
                  className={`containerStyle ${item.id === selectedImageId ? 'selected' : ''}`}
                  key={item.id}
                  onClick={() => handleImageClick(item, index)}
                  style={{ position: 'relative' }}
                >
                  <img
                    id={`image-${item.id}`}
                    src={item.big_img}
                    className='imageStyle'
                    alt='Big Image'
                  />
                  {item.isCorrect && showTick && (
                    <img src={item.green_tick} alt='Green Tick' style={{ position: 'absolute' }} />
                  )}
                </div>
              ))}
            </div>
          </div>
        )}

        {soundFilesEnded && (
          <PlayAgain PlayAgainButton={PlayAgainButton} setCurrentLevelState={async () => {
            await localStorage.setItem('currentLevel', 2);
            window.location.reload()
            setCurrentLevelState(2)
          }} />
        )}

      </div>
      {showMascot && <audio src={VBMV_A_L1_1} onEnded={handleAudioEnd} autoPlay />}
      {showSecondMascot && <audio src={VBMV_A_L1_2} onEnded={handleAudioEndSecond} autoPlay />}
      {playThirdAudio && <audio src={VBMV_A_L1_3} onEnded={handleThirdAudioEnd} autoPlay />}

    </div>
  );
};

export default Index;
