import './App.css';
import React, { useState, useEffect } from 'react';
import ".././node_modules/bootstrap/dist/css/bootstrap.min.css";
import ".././node_modules/bootstrap/dist/js/bootstrap.bundle";
import Level0 from './Level0';
import Level1 from './Level1';
import Level2 from './Level2';
import Level3 from './Level3';
import Level4 from './Level4';
import { appData } from './constants';



const loadLevel = (currentLevelState, setCurrentLevelState) => {
  switch (Number(currentLevelState)) {
    case 0:
      return <Level0 setCurrentLevelState={setCurrentLevelState} />
    case 1:
      return <Level1 setCurrentLevelState={setCurrentLevelState} />
    case 2:
      return < Level2 setCurrentLevelState={setCurrentLevelState} />
    case 3:
      return <Level3 setCurrentLevelState={setCurrentLevelState} />
    case 4:
      return <Level4 setCurrentLevelState={setCurrentLevelState} />
    default:
      return <Level0 setCurrentLevelState={setCurrentLevelState} />
  }
}

function App() {
  const [currentLevelState, setCurrentLevelState] = useState(0)
  useEffect(() => {
    const currentLevel = localStorage.getItem('currentLevel') || 0;
    setCurrentLevelState(currentLevel)
  }, [])
  useEffect(() => {
    const message = window.message || appData
    window.postMessage({ ...message, currentLevel: { "level": currentLevelState, "presentationCompleted": 0 } })
  }, [currentLevelState])
  return (
    <div className="App">
      {loadLevel(currentLevelState, setCurrentLevelState)}
    </div>
  );
}

export default App;


