    setShowGridSet(true);
    const audioReplay = new Audio(GL_A_19);
    setShowAudioReplayCursor(true);
    const nextAudio = new Audio(VBMV_A_L4_8);
    audioReplay.addEventListener("ended", () => {
      nextAudio.play();
      setShowAudioReplayCursor(false);
      setAudioReplayEnded(true);
    });

