import React, { useState, useRef, useEffect } from "react";
import "./index.scss";
import Header from "../components/common/Header";
import lets_play from "../../assets/common/textures/interactions/lets_play.png";
import Playagain from "../../assets/common/textures/interactions/Playagain.png";
import Playgame from "../../assets/common/textures/interactions/Playgame.png";
import Audio_Replay from "../../assets/common/textures/interactions/Audio_Replay.png";
import inactive_audio_replay from "../../assets/common/textures/interactions/inactive_audio_replay.png";
import Hand_Cursor3 from "../../assets/common/textures/interactions/Hand_Cursor3.png";
import Mascot3 from "../../assets/common/textures/mascot/Mascot3.png";
import Mascot1 from "../../assets/common/textures/mascot/Mascot1.png";
import Speech_Bubble from "../../assets/common/textures/interactions/Speech_Bubble.png";
import Speech_Bubble_2 from "../../assets/common/textures/interactions/Speech_Bubble_2.png";
import VBMV_A_L3_1 from "../../assets/level3/audio/VBMV_A_L3_1.mp3";
import VBMV_A_L3_2 from "../../assets/level3/audio/VBMV_A_L3_2.mp3";
import VBMV_A_L3_3 from "../../assets/level3/audio/VBMV_A_L3_3.mp3";
import VBMV_A_L3_4 from "../../assets/level3/audio/VBMV_A_L3_4.mp3";
import VBMV_A_L3_5 from "../../assets/level3/audio/VBMV_A_L3_5.mp3";
import capsicum_green_peas from "../../assets/level3/images/Images/capsicum_green_peas.png";
import VBMV_A_L3_35 from "../../assets/level3/audio/VBMV_A_L3_35.mp3";
import wood_rack_level_3 from "../../assets/level3/images/wood_rack_level_3.png";
import Background_music_1 from "../../assets/common/audio/Background_music_1.mp3";
import For_level_completion from "../../assets/common/audio/For_level_completion.mp3";
import GL_A_19 from "../../assets/common/audio/GL_A_19.mp3";
import GL_A_5 from "../../assets/common/audio/GL_A_5.mp3";
import Threedots from "../components/common/Threedots/Threedots";
// import Canvas from '../components/common/Canvas_Confetti/canvas';
import PlayAgain from "../components/common/Play_Agian/PlayAgain";
import { level3DemoData, level3Data } from "../constants/level3";
import Mascot from "../components/common/Mascot";
import { shuffleArray } from "../helper";

let wrongAnswerFlag = false;

const Index = ({ setCurrentLevelState }) => {
  const [startPlay, setStartPlay] = useState(true);
  const [playGame, setPlayGame] = useState(true);
  const [showMascot, setShowMascot] = useState(false);
  const [showSecondMascot, setShowSecondMascot] = useState(false);
  const [playThirdAudio, setPlayThirdAudio] = useState(false);
  const [isMusicPlaying, setMusicPlaying] = useState(true);
  const [shouldPlayMusic, setShouldPlayMusic] = useState(true);
  const [showGrid, setShowGrid] = useState(false);
  const [showHand, setShowHand] = useState(false);
  const [soundFilesEnded, setSoundFilesEnded] = useState(false);
  const [levelData, setLevelData] = useState([...level3DemoData]);
  const [showGreenTick, setShowGreenTick] = useState(false);
  const [showCursorHand, setShowCursorHand] = useState(false);
  const [playFourthAudio, setPlayFourthAudio] = useState(false);
  const [playLevelThreeAudio, setPlayLevelThreeAudio] = useState(false);
  const [showGridSet, setShowGridSet] = useState(false);
  const [levelData1, setLevelData1] = useState(level3Data[0]);
  const [showModal, setShowModal] = useState({
    display: false,
    index: 0,
  });
  const [selectedImageId, setSelectedImageId] = useState(null);
  const [showTick, setShowTick] = useState(false);
  const [isCorrectImageClicked, setIsCorrectImageClicked] = useState(false);
  const [gameIndex, setGameIndex] = useState(0);
  const [showAudioReplayCursor, setShowAudioReplayCursor] = useState(false);
  const [audioReplayEnded, setAudioReplayEnded] = useState(false);
  // Audio replay button state
  const [isAudioPlaying, setIsAudioPlaying] = useState(false);
  const [isButtonDisabled, setIsButtonDisabled] = useState(false);
  const [isAudioReplayButtonActive, setIsAudioReplayButtonActive] =
    useState(true);

  const [score, setScore] = useState(20);
  const [progress, setProgress] = useState(0);

  const updateScore = (newScore) => {
    setScore(newScore);
  };

  /**
   * Mute Unmute Function
   */
  const handleMusicButtonClick = () => {
    setShouldPlayMusic(!shouldPlayMusic);
    setMusicPlaying(!isMusicPlaying);
  };

  useEffect(() => {
    const audio = new Audio(Background_music_1);

    if (shouldPlayMusic && isMusicPlaying) {
      audio.loop = true;
      audio.play();
    } else {
      audio.pause();
    }

    return () => {
      audio.pause();
      audio.currentTime = 0;
    };
  }, [shouldPlayMusic, isMusicPlaying]);

  useEffect(() => {
    setTimeout(() => {
      if (gameIndex < level3Data.length) {
        let gameArray = [...level3Data[gameIndex]]
        gameArray = shuffleArray(gameArray)
        setLevelData1(gameArray);
        setShowTick(false);
        setIsCorrectImageClicked(false);
        setTimeout(() => {
          gameIndex > 0 &&
            gameIndex < level3Data.length &&
            new Audio(level3Data[gameIndex][0].find_vegetable).play();
        }, 500);
      }
      if (gameIndex === level3Data.length) {
        setShowGridSet(false);
        const audio = new Audio(For_level_completion);
        audio.addEventListener("play", () => {
          setScore(30);
          var count = 200;
          var defaults = {
            origin: { y: 0.7 },
          };

          function fire(particleRatio, opts) {
            confetti(
              Object.assign({}, defaults, opts, {
                particleCount: Math.floor(count * particleRatio),
              })
            );
          }

          fire(0.25, {
            spread: 26,
            startVelocity: 55,
          });
          fire(0.2, {
            spread: 60,
          });
          fire(0.35, {
            spread: 100,
            decay: 0.91,
            scalar: 0.8,
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 25,
            decay: 0.92,
            scalar: 1.2,
          });
          fire(0.1, {
            spread: 120,
            startVelocity: 45,
          });
        });
        audio.play();

        setSoundFilesEnded(true);
        const playNewGameAudio = new Audio(GL_A_5);
        playNewGameAudio.play();

      }
    }, 1500);
  }, [gameIndex, setGameIndex]);

  useEffect(() => { }, [showCursorHand, setShowCursorHand]);

  /**
   * onclick play button
   */
  const PlayButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
    setScore(20);
  };
  /**
   * onclick play AGAIN button
   */
  const PlayAgainButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
    setSoundFilesEnded(false);
    setScore(20);
    window.location.reload();
  };

  /**
   * First Audio End
   */
  const handleAudioEnd = () => {
    setShowMascot(false);
    setShowSecondMascot(true);
    setShowGrid(true);

  };

  /**
   * Second Audio End
   */
  const handleAudioEndSecond = () => {
    setPlayThirdAudio(true);
    setShowSecondMascot(false);
  };

  // play third audio and fourth audio
  useEffect(() => {
    if (playLevelThreeAudio) {
      const audio = new Audio(VBMV_A_L3_35);
      audio.addEventListener("ended", () => {
        setPlayFourthAudio(true);
      });
      audio.play();
    }
  }, [playLevelThreeAudio]);

  const handleThirdAudioEnd = () => {
    setShowCursorHand(true);
    setTimeout(() => {
      setPlayLevelThreeAudio(true);
    }, 1500);
  };

  // when fourth audio end call this function
  const handleFourthAudioEnd = () => {
    setShowGrid(false);
    setShowGridSet(true);
    const audioReplay = new Audio(GL_A_19);
    setShowAudioReplayCursor(true);
    const nextAudio = new Audio(VBMV_A_L3_5);
    audioReplay.addEventListener("ended", () => {
      nextAudio.play();
      setShowAudioReplayCursor(false);
      setAudioReplayEnded(true);
    });

    audioReplay.play();
  };

  // handleImageClick function
  const handleImageClick = (item, index) => {
    console.log(gameIndex, wrongAnswerFlag);
    if (isCorrectImageClicked) {
      // Disable image click if correct image is already clicked
      return;
    }

    if (gameIndex === level3Data.length - 1) {
      if (wrongAnswerFlag) {
        for (let i = 0; i < 3; i++) {
          level3Data.push(level3Data[Math.floor(Math.random() * 10)]);
        }
        console.log(level3Data);
        wrongAnswerFlag = false;
      } else {
        let updatedProgress = progress;
        updatedProgress = updatedProgress + 25;
        setProgress(updatedProgress);
      }
    }

    // Disable image click
    setIsCorrectImageClicked(true);

    setSelectedImageId(item.id);

    if (item.isCorrect) {
      const correctSound = new Audio(item.correct_sound);
      correctSound.addEventListener("ended", () => {
        const correctAnswer = new Audio(item.correct_answer);
        correctAnswer.addEventListener("ended", () => {
          setIsAudioReplayButtonActive(true);
          // Enable image click after correct_answer audio ends
          setIsCorrectImageClicked(false);
        });
        correctAnswer.addEventListener("ended", () => {
          // Increase game index and set next image after correct_answer audio ends
          let i = gameIndex;
          i++;
          setTimeout(() => {
            setGameIndex(i);
          }, 0);
        });
        correctAnswer.play();
      });
      correctSound.play();

      // setShowCursorHand(true);
      setShowTick(true);

      if ([2, 4, 7].includes(gameIndex + 1)) {
        let updatedProgress = progress;
        updatedProgress = updatedProgress + 25;
        setProgress(updatedProgress);
      }
    } else {
      if (gameIndex > 6) wrongAnswerFlag = true;
      const wrongSound = new Audio(item.wrong_sound);
      wrongSound.addEventListener("ended", () => {
        const tryAgainSound = new Audio(item.try_again_sound);
        tryAgainSound.addEventListener("ended", () => {
          // Enable image click after try_again_sound audio ends
          setIsCorrectImageClicked(false);
        });
        tryAgainSound.play();
      });
      wrongSound.play();

      const wrongImage = document.getElementById(`image-${item.id}`);
      wrongImage.classList.add("rise-shake");
      setTimeout(() => {
        wrongImage.classList.remove("rise-shake");
      }, 830);
    }
  };

  // gameInstructionAudio
  const gameInstructionAudio = () => {
    if (!isButtonDisabled) {
      const currentGameIndex = gameIndex % levelData1.length;
      const currentItem = levelData1[currentGameIndex];

      if (currentItem) {
        const findVegetableAudio = new Audio(currentItem.find_vegetable);

        findVegetableAudio.addEventListener("play", () => {
          setIsCorrectImageClicked(true);
          setIsAudioPlaying(true);
          setIsButtonDisabled(true);
        });

        findVegetableAudio.addEventListener("ended", () => {
          setIsCorrectImageClicked(false);
          setIsAudioPlaying(false);
          setIsButtonDisabled(false);
        });

        findVegetableAudio.play();
      }
    }
  };



  return (
    <div className="level3Styles">
      <div className="level0_div">
        <div>
          <Header
            onMusicButtonClick={handleMusicButtonClick}
            isMusicPlaying={isMusicPlaying}
            score={score}
            selectedLevel={3}
            percentFill={progress}
          />
          <div className="audio_replay d-flex justify-content-end">
            <div className="audio_replay_button">
              {audioReplayEnded && (
                <button
                  className="btn p-0"
                  type="button"
                  onClick={() => gameInstructionAudio()}
                  disabled={isButtonDisabled}
                >
                  {isAudioPlaying || isAudioReplayButtonActive ? (
                    <img
                      src={Audio_Replay}
                      className="img-fluid Audio_Replay_Btn"
                      alt="Audio_Replay"
                    />
                  ) : (
                    <img
                      src={inactive_audio_replay}
                      className="img-fluid Audio_Replay_Btn"
                      alt="Audio_Replay"
                    />
                  )}
                </button>
              )}
              {!audioReplayEnded && (
                <img
                  src={inactive_audio_replay}
                  className="img-fluid Audio_Replay_Btn"
                  alt="Audio_Replay"
                />
              )}
              {showAudioReplayCursor && (
                <img
                  src={Hand_Cursor3}
                  className="audio_replay"
                  alt="hand_cursor"
                />
              )}
            </div>
          </div>
        </div>
        {startPlay && (
          <div className="play_btn_div">
            <button
              className="btn play_btn p-0"
              type="button"
              onClick={() => {
                PlayButton();
              }}
            >
              <img
                src={lets_play}
                className="play_img img-fluid"
                alt="play_btn"
              />
            </button>
            <span></span>
          </div>
        )}
        {showMascot && (
          <Mascot />
        )}

        {showSecondMascot && (
          <Mascot showMascot2={true} />
        )}

        {showGrid && (
          <div className="game_div_table">
            <div className="container">
              <div className="row">
                <div className="col-sm-3"></div>
                {levelData.slice(0, 3).map((item, index) => (
                  <div className="col-sm-2 first_row_pp" key={item.id}>
                    <img
                      src={item.img}
                      className="img-fluid level_3_vegetable_box"
                      alt="Big Image"
                    />
                    {!showCursorHand && index === 0 && (
                      <img
                        src={item.hand_cursor}
                        className="hand_cursor"
                        alt="hand_cursor"
                      />
                    )}
                    {showCursorHand && index === 0 && (
                      <img
                        src={item.green_tick}
                        className="greenTick"
                        alt="Green Tick"
                      />
                    )}
                  </div>
                ))}
                <div className="col-sm-3"></div>
              </div>
              <div className="row second_row">
                <div className="col-sm-12 d-flex justify-content-center wood_rack">
                  <img
                    src={wood_rack_level_3}
                    className="img-fluid wood_rack_level"
                    alt="Big Image"
                  />
                </div>
              </div>
            </div>
            <div className="container game_div_table_2">
              <div className="row">
                <div className="col-sm-3"></div>
                {levelData.slice(3, 6).map((item, index) => (
                  <div className="col-sm-2" key={item.id}>
                    <img
                      src={item.img}
                      className="img-fluid level_3_vegetable_box"
                      alt="Big Image"
                    />
                  </div>
                ))}
                <div className="col-sm-3"></div>
              </div>
              <div className="row">
                <div className="col-sm-12 d-flex justify-content-center">
                  <img
                    src={wood_rack_level_3}
                    className="img-fluid wood_rack_level"
                    alt="Big Image"
                  />
                </div>
              </div>
            </div>
          </div>
        )}

        {showGrid && showCursorHand && (
          <>
            <audio
              src={levelData[0].correct_sound}
              autoPlay
              onEnded={handleThirdAudioEnd}
            />
            {playFourthAudio && (
              <audio
                src={VBMV_A_L3_4}
                autoPlay
                onEnded={handleFourthAudioEnd}
              />
            )}
          </>
        )}

        {showGridSet && (
          <div className="game_div_table">
            <div className="container">
              <div className="row">
                <div className="col-sm-3"></div>
                {levelData1.slice(0, 3).map((item, index) => (
                  <div className="col-sm-2" key={item.id}>
                    <div
                      className={`containerStyle ${item.id === selectedImageId ? "selected" : ""
                        }`}
                      onClick={() => handleImageClick(item, index)}
                      style={{ position: 'relative' }}
                    >
                      <img
                        id={`image-${item.id}`}
                        src={item.img}
                        className="img-fluid level_3_vegetable_box"
                        alt="Big Image"
                      />
                      {item.isCorrect && showTick && (
                        <img
                          src={item.green_tick}
                          className="greenTick"
                          alt="Green Tick"
                        />
                      )}
                    </div>
                  </div>
                ))}
                <div className="col-sm-3"></div>
              </div>
              <div className="row second_row">
                <div className="col-sm-12 d-flex justify-content-center wood_rack">
                  <img
                    src={wood_rack_level_3}
                    className="img-fluid wood_rack_level"
                    alt="Big Image"
                  />
                </div>
              </div>
            </div>
            <div className="container game_div_table_2">
              <div className="row">
                <div className="col-sm-3"></div>
                {levelData1.slice(3, 6).map((item, index) => (
                  <div className="col-sm-2" key={item.id}>
                    <div
                      className={`containerStyle ${item.id === selectedImageId ? "selected" : ""
                        }`}
                      onClick={() => handleImageClick(item, index + 3)}
                      style={{ position: 'relative' }}
                    >
                      <img
                        id={`image-${item.id}`}
                        src={item.img}
                        className="img-fluid level_3_vegetable_box"
                        alt="Big Image"
                      />
                      {item.isCorrect && showTick && (
                        <img
                          src={item.green_tick}
                          className="greenTick"
                          alt="Green Tick"
                        />
                      )}
                    </div>
                  </div>
                ))}
                <div className="col-sm-3"></div>
              </div>
              <div className="row">
                <div className="col-sm-12 d-flex justify-content-center">
                  <img
                    src={wood_rack_level_3}
                    className="img-fluid wood_rack_level"
                    alt="Big Image"
                  />
                </div>
              </div>
            </div>
          </div>
        )}


        {soundFilesEnded && <PlayAgain PlayAgainButton={PlayAgainButton} setCurrentLevelState={async () => {
          await localStorage.setItem('currentLevel', 4);
          window.location.reload()
          setCurrentLevelState(4)
        }} />}
      </div>
      {showMascot && (
        <audio src={VBMV_A_L3_1} onEnded={handleAudioEnd} autoPlay />
      )}
      {showSecondMascot && (
        <audio src={VBMV_A_L3_2} onEnded={handleAudioEndSecond} autoPlay />
      )}
      {playThirdAudio && (
        <audio src={VBMV_A_L3_3} onEnded={handleThirdAudioEnd} autoPlay />
      )}
    </div>
  );
};

export default Index;
