import React, { useState, useRef, useEffect } from "react";
import "./index.scss";
import Header from "../components/common/Header";
import lets_play from "../../assets/common/textures/interactions/lets_play.png";
import Playagain from "../../assets/common/textures/interactions/Playagain.png";
import Playgame from "../../assets/common/textures/interactions/Playgame.png";
import Mascot3 from "../../assets/common/textures/mascot/Mascot3.png";
import Mascot1 from "../../assets/common/textures/mascot/Mascot1.png";
import Speech_Bubble from "../../assets/common/textures/interactions/Speech_Bubble.png";
import Speech_Bubble_2 from "../../assets/common/textures/interactions/Speech_Bubble_2.png";
import VBMV_A_L0_1 from "../../assets/level0/audio/VBMV_A_L0_1.mp3";
import VBMV_A_L0_2 from "../../assets/level0/audio/VBMV_A_L0_2.mp3";
import VBMV_A_L0_3 from "../../assets/level0/audio/VBMV_A_L0_3.mp3";
import GL_A_5 from "../../assets/common/audio/GL_A_5.mp3";
import Background_music_1 from "../../assets/common/audio/Background_music_1.mp3";
import Threedots from "../components/common/Threedots/Threedots";
import { level0Data } from "../constants";
import Modal from "../components/Level0/modal";
import Mascot from "../components/common/Mascot";

var index = 0;

const Index = ({ setCurrentLevelState }) => {
  const [startPlay, setStartPlay] = useState(true);
  const [playGame, setPlayGame] = useState(true);
  const [showMascot, setShowMascot] = useState(false);
  const [showSecondMascot, setShowSecondMascot] = useState(false);
  const [playThirdAudio, setPlayThirdAudio] = useState(false);
  const [isMusicPlaying, setMusicPlaying] = useState(true);
  const [shouldPlayMusic, setShouldPlayMusic] = useState(false);
  const [showGrid, setShowGrid] = useState(false);
  const [showModal, setShowModal] = useState({
    display: false,
    index: 0,
  });

  const [soundFilesEnded, setSoundFilesEnded] = useState(false);

  const [levelData, setLevelData] = useState([...level0Data]);

  /**
   * Mute Unmute Function
   */
  const handleMusicButtonClick = () => {
    setShouldPlayMusic(!shouldPlayMusic);
    setMusicPlaying(!isMusicPlaying);
  };

  useEffect(() => {
    const audio = new Audio(Background_music_1);

    if (shouldPlayMusic && isMusicPlaying) {
      audio.loop = true;
      audio.play();
    } else {
      audio.pause();
    }

    return () => {
      audio.pause();
      audio.currentTime = 0;
    };
  }, [shouldPlayMusic, isMusicPlaying]);

  // const playSoundFiles = () => {
  //   let currentIndex = 0;

  //   const playNextSound = () => {
  //     if (currentIndex < level0Data.length) {
  //       const item = level0Data[currentIndex];
  //       const audio = new Audio(item.sound);
  //       audio.onended = () => {
  //         currentIndex++;
  //         playNextSound();
  //       };
  //       audio.play();
  //     }
  //   };
  //   playNextSound();
  // };

  /**
   * chnage Image inactive to active
   */
  useEffect(() => {
    const newArray = levelData.filter((item) => item.disabled === true);
    if (newArray.length === levelData.length) {
      setTimeout(() => {

        levelData.forEach((item) => {
          item.disabled = false;
        });
        playSoundFiles(); // Call the function to play sound files
        setLevelData(levelData);
        setShowModal({ ...showModal, index: 0 });

      }, 3000)
    }
  }, [levelData]);

  /**
   * Play sound Auto After tapping all vegetables
   */
  const playSoundFiles = () => {

    const audio = new Audio(levelData[index].sound);
    const containers = document.querySelectorAll(".containerStyle");

    audio.onplay = () => {
      containers[index].classList.add("active");
    };
    audio.onended = () => {
      containers[index].classList.remove("active");
      index++;
      if (index < level0Data.length) {
        playSoundFiles()
      }


      if (index === level0Data.length) {
        setShowGrid(false);
        setSoundFilesEnded(true);
        const playNewGameAudio = new Audio(GL_A_5);
        playNewGameAudio.play();
      }
    };
    audio.play();


  };

  /**
   * onclick play button
   */
  const PlayButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
  };
  /**
   * onclick play button
   */
  const PlayAgainButton = () => {
    setShouldPlayMusic(true);
    setStartPlay(false);
    setShowMascot(true);
    setSoundFilesEnded(false);
    window.location.reload();
  };

  /**
   * First Audio End
   */
  const handleAudioEnd = () => {
    setShowMascot(false);
    setShowSecondMascot(true);
  };

  /**
   * Second Audio End
   */
  const handleAudioEndSecond = () => {
    setPlayThirdAudio(true);
    setShowSecondMascot(false);
    setShowGrid(true);
  };

  /**
   * On modal close
   */
  const handleModalClose = () => {
    setShowModal({ ...showModal, display: false });
    const newArray = [...levelData];
    newArray[showModal.index].disabled = true;
    setLevelData(newArray);
  };
  return (
    <div className='level0Style'>
      <div className="level0_div">
        <Header
          onMusicButtonClick={handleMusicButtonClick}
          isMusicPlaying={isMusicPlaying}
        />
        {startPlay && (
          <div className="play_btn_div">
            <button
              className="btn play_btn p-0"
              type="button"
              onClick={() => {
                PlayButton();
              }}
            >
              <img
                src={lets_play}
                className="play_img img-fluid"
                alt="play_btn"
              />
            </button>
            <span></span>
          </div>
        )}
        {showMascot && <Mascot />}

        {showSecondMascot && (
          <Mascot showMascot2={true} />
        )}

        {showGrid && (
          <div className="vegetableGridContainer">
            <div className="vegetablesGrid">
              {levelData.map((item, index) => {
                return (
                  <div
                    className="containerStyle"
                    key={item.id}
                    onClick={
                      item.disabled
                        ? () => { }
                        : () =>
                          setShowModal({ ...showModal, display: true, index })
                    }
                  >
                    {(item.disabled && (
                      <img src={item.inactive_img} className="imageStyle" />
                    )) || <img src={item.img} className="imageStyle" />}
                  </div>
                );
              })}
            </div>
          </div>
        )}
        {soundFilesEnded && (
          <div className="play_agian_next_div">
            <div className="Playagain_div">
              <button
                className="btn p-0"
                type="button"
                onClick={() => {
                  PlayAgainButton();
                }}
              >
                <img
                  src={Playagain}
                  className="my_play_btn img-fluid"
                  alt="Playagain"
                />
              </button>
            </div>
            <div className='Playgame_div'>
              <button
                className="btn"
                type="button"
                onClick={async () => {
                  await localStorage.setItem('currentLevel', 1);
                  window.location.reload()
                  setCurrentLevelState(1)
                }}
              >
                <img src={Playgame} className="my_play_btn img-fluid" alt="Playgame" />
              </button>
            </div>
          </div>
        )}
      </div>
      {showMascot && (
        <audio src={VBMV_A_L0_1} onEnded={handleAudioEnd} autoPlay />
      )}
      {showSecondMascot && (
        <audio src={VBMV_A_L0_2} onEnded={handleAudioEndSecond} autoPlay />
      )}
      {playThirdAudio && <audio src={VBMV_A_L0_3} autoPlay />}
      <div className="d-flex justify-content-center align-content-center modal_div">

        <Modal showModal={showModal} setShowModal={setShowModal}>
          <img src={levelData[showModal.index].big_img} className="modal_image" />
          <audio
            src={levelData[showModal.index].sound}
            autoPlay
            onEnded={handleModalClose}
          ></audio>
        </Modal>
      </div>
    </div>
  );
};

export default Index;
