import React from 'react';
import './Progress.css';


const PROGRESS_CONSTANT = [1, 2, 3, 4]

const showProgress = (item) => {
    if (item === selectedLevel) {
        return
    }
}

const ProgessBar = ({ selectedLevel = 0, percentFill = 0 }) => {
    return (
        <div className='progressBarContainer'>
            {PROGRESS_CONSTANT.map((item) => {
                return (
                    <div className='progressLevel' style={item === selectedLevel ? { color: 'black' } : {}} key={item}>
                        LEVEL {item}
                        <div className="progress" role="progressbar" aria-label="Basic example"
                            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100"
                            style={percentFill === 0 && item === selectedLevel ? {
                                width: '80%',
                                height: '2vh',
                                border: '1px solid black',
                                bordeRadius: 3
                            } : {
                                width: '80%',
                                height: '2vh',
                                border: '1.5px solid #AA6427',
                                bordeRadius: 3
                            }}
                        >
                            {
                                item < selectedLevel && (<div className={`progress-bar progessBarStyle`} style={{
                                    width: `100%`
                                }}></div>)
                            }
                            <div className={`progress-bar progessBarStyle`} style={{
                                width: `${item === selectedLevel ? percentFill : 0}%`
                            }}></div>
                        </div>

                    </div>
                )
            })}

        </div>
    )
}

const ProgessLevelBar = () => {
    return (
        <div class="progress" role="progressbar" aria-label="Basic example"
            aria-valuenow="100" aria-valuemin="0" aria-valuemax="100"
            style={{ width: '60%', height: 10 }}
        >
            <div className={`progress-bar w-${100} progessBarStyle`}></div>
        </div>
    )
}

export default ProgessBar