import React from 'react'
import './PlayAgain.css';
import Playagain from '../../../../assets/common/textures/interactions/Playagain.png';
import Playgame from '../../../../assets/common/textures/interactions/Playgame.png';
import Play_the_next_level from '../../../../assets/common/textures/interactions/Play_the_next_level.png';


const PlayAgain = ({ PlayAgainButton, setCurrentLevelState }) => {
    return (
        <>
            <div className="play_agian_next_div">
                <div className='Playagain_div'>
                    <button
                        className="btn p-0"
                        type="button"
                        onClick={() => {
                            PlayAgainButton();
                        }}
                    >
                        <img src={Playagain} className="my_play_btn img-fluid" alt="Playagain" />
                    </button>
                </div>
                <div className='Playgame_div'>
                    <button
                        className="btn"
                        type="button"
                        onClick={async () => {
                            await localStorage.setItem('currentLevel', 1);
                            setCurrentLevelState()
                        }}
                    >
                        <img src={Play_the_next_level} className="Play_the_next_level img-fluid" alt="Play_the_next_level" />
                    </button>
                </div>
            </div>
        </>
    )
}

export default PlayAgain